import React, { useEffect, useState } from 'react'
import { ConfigProvider, Row, Col, Space, Skeleton } from "antd"
import { Avatar, List } from 'antd';
import { Input } from 'antd';
import axios from 'axios';
import { BASEURL } from '../configs';
const { Search } = Input;


const INITIAL_SKILLS = [
  {
    "_id": "63b1418f9065bdbc93136e19",
    "title": "Spring Boot",
    "description": "Spring Boot makes it easy to create stand-alone, production-grade Spring based Applications",
    "keywords": [
      "spring",
      "boot",
      "spring boot",
      "spring_boot"
    ],
    "users": [],
    "skill_id": "52d3581c-cf3f-4ccf-8bc3-531e34d18ebb",
    "__v": 50
  },
  {
    "_id": "63b177e8b98a1fcaf9aa1cb1",
    "title": "React JS",
    "description": "Spring Boot makes it easy to create stand-alone, production-grade Spring based Applications",
    "keywords": [
      "react",
      "react.js",
      "react js",
      "react_js"
    ],
    "users": [],
    "skill_id": "455bc41c-5add-4222-85e8-1a4d394a4317",
    "__v": 78
  }
];

function ProfilesList() {
  const fakeDataUrl = `${BASEURL}/user`;
  const [data, setData] = useState([]);
  const [list, setList] = useState([]);
  const [skills, setSkills] = useState(INITIAL_SKILLS);
  const initList = async (searchKey) => {
    const resonse = await axios.get(`${BASEURL}/skill/name/${searchKey}`);
    if (!resonse.data) {
      setData([]);
      setList([]);
    } else {
      setList(resonse.data.users);
      setData(resonse.data.users);
    }
  }
  const initSkills = async () => {
    const response = await axios.get(`${BASEURL}/skill`);
    setSkills(response.data);
  }
  const onSearch = (value) => {
    initList(value);
  };
  const onChange = e => {
    const value = e.target.value.trim();
    if (value == "") {
      fetch(fakeDataUrl)
        .then((res) => res.json())
        .then((res) => {
          setData(res.data);
          setList(res.data);
        });
    } else {
      for (let skill of skills) {
        if (skill.keywords.includes(value.toLowerCase())) {
          setList(skill.users);
          setData(skill.users);
        }
      }
    }
  }
  useEffect(() => {
    initSkills();
    fetch(fakeDataUrl)
      .then((res) => res.json())
      .then((res) => {
        setData(res.data);
        setList(res.data);
      });
  }, []);
  console.log(list)
  return (
    <div className='' style={{ padding: '2rem' }}>
      <Space direction="vertical" size="large" style={{ display: 'flex' }}>
        <div>
          <Row>
            <Col span={4} offset={4}>
              <h1>Find people....</h1>
            </Col>
          </Row>

        </div>
        <div>
          <Row>
            <Col span={12} offset={6}>
              <ConfigProvider
                theme={{
                  token: {

                    colorPrimary: "#E63946"

                  },
                }}>

                <Search
                  placeholder="input search text"
                  allowClear
                  enterButton="Search"
                  size="large"
                  onChange={onChange}
                  onSearch={onSearch}
                />
              </ConfigProvider>

            </Col>
          </Row>
        </div>
        <div>
          <ConfigProvider
            theme={{
              token: {
                colorPrimary: "#E63946",
                colorTextBase: '#F1FAEE',

              },
            }}>


            <List
              className="demo-loadmore-list"
              itemLayout="horizontal"
              dataSource={list}

              renderItem={(item) => (
                <List.Item
                  actions={[<a key="list-loadmore-more">more</a>]}

                >
                  <Skeleton avatar title={false} loading={false} active>
                    <List.Item.Meta
                      avatar={<Avatar src="data:image/png;base64," />}
                      title={<a href={`/user/${item.user_id}`}>{item.name}</a>}
                      description={item.summary}
                    />
                    <div>{item.email}</div>
                  </Skeleton>
                </List.Item>
              )}
            />
          </ConfigProvider>
        </div>
      </Space>
    </div>
  )
}

export default ProfilesList